$borderColor: #e1e3e4;

$primaryColor: #1e2d7d;
$secondaryColor: #00badb;

$textColor: #677279;
$darkBackgroundTextColor: #fff;
$placeholderTextColor: #cccccc;

$dangerColor: #ee0000;

body {
  background-color: #f3f5f6;
  color: $textColor;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
    "Helvetica Neue", sans-serif;
}

a {
  color: $secondaryColor;
  cursor: pointer;
}

h2 {
  color: $primaryColor;
  font-size: 32px;
}

input,
select,
textarea {
  padding: 8px 16px;
  border: 1px solid $borderColor;
  border-radius: 4px;
  margin: 4px 0;
}


input[type="checkbox"] {
  margin: 2px 8px 2px 2px;
  width: 16px;
  height: 16px;
  cursor: pointer;
}

input[type="submit"] {
  background-color: $secondaryColor;
  padding: 16px 48px;
  color: $darkBackgroundTextColor;
  border: none;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  transition: background-color 0.2s;

  &:hover {
    background-color: rgba(0, 186, 219, 0.8);
  }
}

textarea {
  height: 200px;
}

label {
  margin-top: 15px;
  span {
    color: $dangerColor;
  }
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: $placeholderTextColor;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: $placeholderTextColor;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: $placeholderTextColor;
}

header {
  background-color: #1e2d7d;
  padding: 20px 40px;

  .logo {
    width: 144px;
    height: 66px;
    background-image: url(data:image/png;base64,);
    background-size: cover;
  }
}

nav {
  display: block;
  position: relative;
  left: 0;
  width: 100%;
  border-bottom: 1px solid $borderColor;
  background: $darkBackgroundTextColor;

  .container {
    padding: 0 40px;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      .nav-bar__item {
        position: relative;
        display: inline-block;
        margin-right: 35px;
        padding: 17px 0;

        .nav-bar__link {
          display: block;
          cursor: pointer;
          text-decoration: none;
          color: $textColor;
          font-weight: 500;
          transition: color 0.2s;

          &:hover {
            color: $secondaryColor;
          }
        }
      }
    }
  }
}

.main {
  padding: 20px 40px;
  margin: 0 auto;
  max-width: 500px;
  form {
    display: flex;
    flex-direction: column;

    .addressRow {
      display: flex;
      gap: 8px;
      input {
        flex-grow: 1;
        flex-shrink: 1;
        width: 33%;
      }
    }

    .checkBoxRow {
      margin-top: 8px;
      display: flex;
      align-items: center;

      label {
        margin: 0;
        cursor: pointer;
      }
    }

    .description {
      font-size: 12px;
      opacity: 0.6;
      margin-left: 26px;
    }

    input[type="submit"] {
      margin: 30px 0 100px 0;
    }
  }
}

footer {
  text-align: center;
  opacity: 0.6;
}
